import React from 'react';
import './App.css';

import { Layout, Menu, Breadcrumb, Icon, Select} from 'antd';
import { Table, Tag } from 'antd';

import {BrowserRouter, Route, Switch, Link} from 'react-router-dom';

import ReactEcharts from 'echarts-for-react';

const { SubMenu } = Menu;
const { Header, Content, Sider,Footer } = Layout;
const {Option} = Select;



function App() {
  return (
    <div className="App">
      <MyLayout />
    </div>
  );
}

class MyLayout extends React.Component {
  render() {
    return(
      <Layout style={{height: '100%'}}>
        <Header className="header"></Header>
        <BrowserRouter>
          <Layout style={{padding: '0px 25px '}}>
            <Sider width={200} style={{ background: '#fff'}}>
              <Menu 
                mode="inline"
                defaultSelectedKeys={['1']}
                defaultOpenKeys={['sub1']}
                style={{ height: '100%', borderRight: 0}}
              >
                <SubMenu
                  key="sub1"
                  title={
                    <span>
                      <Icon type="user"></Icon>
                      本地数据
                    </span>
                  }
                >
                  <Menu.Item key="1"><Link to="/patent">专利</Link></Menu.Item>
                  <Menu.Item key="2"><Link to="/stock">基金</Link></Menu.Item>
                </SubMenu>
                <SubMenu
                  key="sub2"
                  title={
                    <span>
                      <Icon type="user"></Icon>
                      在线数据
                    </span>
                  }
                >
                  <Menu.Item key="3"><Link to="/test">测试在线获取数据库</Link></Menu.Item>
                  <Menu.Item key="4">option4</Menu.Item>
                </SubMenu>
              </Menu>
            </Sider>
            <Layout style={{ padding: '0 24px 24px' }}>
              <Breadcrumb style={{ margin: '16px 0' }}>
                <Breadcrumb.Item>Home</Breadcrumb.Item>
                <Breadcrumb.Item>List</Breadcrumb.Item>
                <Breadcrumb.Item>App</Breadcrumb.Item>
              </Breadcrumb>
              <Content
                style={{
                  background: '#fff',
                  padding: 24,
                  // margin: 0,
                  // minHeight: 280,
                  height: '100%'
                }}
              >
                <Switch>
                  <Route path="/patent" component={MyPatentTable}></Route>
                  <Route path="/stock" component={MyStock}></Route>
                  <Route path="/test" component={MyPatentTableOnline}></Route>
                  <Route render={()=> <p>Home</p>}></Route>
                </Switch>
              </Content>
            </Layout>
          </Layout>
        </BrowserRouter>
        <Footer style={{ textAlign: 'center' }}>Ant Design ©2019 Created by LeonChi</Footer>
      </Layout>
    )
  }
}

